<template>
   <div id="delProductModal" ref="modal" class="modal fade" tabindex="-1"
  aria-labelledby="delProductModalLabel" aria-hidden="true">
<div class="modal-dialog">
 <div class="modal-content border-0">
   <div class="modal-header bg-danger text-white">
     <h5 id="delProductModalLabel" class="modal-title">
       <span>刪除 {{product.title}}</span>
     </h5>
     <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
   </div>
   <div class="modal-body">
     是否刪除
     <strong class="text-danger">{{ product.title }}</strong> 商品(刪除後將無法恢復)。
   </div>
   <div class="modal-footer">
     <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">
       取消
     </button>
     <button type="button" class="btn btn-danger"  @click="$emit('del-product', product)">
       確認刪除
     </button>
   </div>
 </div>
</div>
</div>`
</template>

<script>
// 匯入 modal 元件
import Modal from 'bootstrap/js/dist/modal'
export default {
  props: {
    // 制定props 物件驗證規則
    product: {
      type: Object,
      dafalut () {
        return {}
      }
    }
  },
  data () {
    return {
      modal: ''
    }
  },
  emits: ['del-product'],
  methods: {
    openModal () {
      this.modal.show()
    },
    hideModal () {
      this.modal.hide()
    }
  },
  mounted () {
    this.modal = new Modal(this.$refs.modal)
  }
}
</script>
